const ImgFreeDisp = props => {

    let content = (
        <div className={props.className}>
            <div className="ralative w-full h-auto">
                <img src={props.src} alt={props.alt} />
                {(props.displayAlt)
                    ? (<p className="absolute bottom-0 left-0 font-lato font-regular text-sm text-white bg-transparent">
                        {props.alt}
                    </p>)
                    : null}
            </div>
        </div>
    )

    return content
}

export default ImgFreeDisp